<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="车辆品牌">
                    <el-select v-model="filters.bus_brand" placeholder="请选择">
                        <el-option
                            v-for="item in optionsCarBrand"
                            :key="item.brand_id"
                            :label="item.bus_brand"
                            :value="item.bus_brand">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button @click="handleSearch">查询</el-button>
            </el-form>
            <el-table :data="tableData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="bus_brand" label="车辆品牌" width="120">
                </el-table-column>
                <el-table-column prop="bus_series" label="车辆系列" width="120">
                </el-table-column>
                <el-table-column prop="bus_type" label="车辆类型">
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import {apiGoodsParamsCarType,apiGetCarBrand} from '@/api/resourceManagement'
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                total: 1,
                filters: {
                    bus_brand:'',
                },
                optionsCarBrand: []
            }
        },
        created() {
            this.getData();
            this.getCarBrand();
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    page: this.page,
                    ...this.filters
                }
                apiGoodsParamsCarType(params).then(res => {
                    this.tableData = res.data;
                    this.total = res.total;
                })
               
            },
            getCarBrand(){
                apiGetCarBrand({}).then(res => {
                    this.optionsCarBrand = res.data;
                })
            },
            handleSearch() {
                this.getData();
            },
            handleDelete(row, index) {
                let params = {
                    id: row.id,
                    operation_uniqid: row.operation_uniqid,
                    status: 2
                }
                API.apiIndividualFileEdit(params).then(res => {
                    this.tableData.splice(index, 1);
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
